import React from "react";
import Grid from "@mui/material/Grid";
import { useMediaQuery } from "@mui/material";

export default function ImgContainer(props) {
  const { margin, images, justify, grid } = props;
  const pc = useMediaQuery("(min-width:1200px)");

  return (
    <Grid
      container
      className="imgSort"
      style={{
        marginTop: `${margin}px`,
        justifyContent: justify,
      }}
    >
      {images.map((obj, i) => (
        <Grid
          item
          xs={12}
          lg={grid}
          key={i}
          className={`${grid === 3 ? "gridSpread" : "gridCenter"}`}
        >
          <a href={obj.link} target="_blank" rel="noreferrer">
            {pc ? (
              <img src={obj.img} width={obj.width} alt={`${obj.img}`} />
            ) : (
              <img
                src={obj.img}
                alt={`${obj.img}`}
                style={{
                  maxHeight: "72px",
                  maxWidth: "200px",
                  height: "auto",
                  width: "auto",
                }}
              />
            )}
          </a>
        </Grid>
      ))}
    </Grid>
  );
}
